___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
}
body {
  font-family: Arial, sans-serif;
  background-color: #f3f3f3;
  display: block;
  height: 100vh;
}
.container {
  width: 880px;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  margin: auto;
  margin-top: 10%;
}
.form-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}
label {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.title, .image_link {
  width: 75%;
  padding: 5px;
  margin-left: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.form-container button {
  align-self: center;
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
.form-container button:hover {
  background-color: #0056b3;
}
.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}
.gallery-item {
  width: calc(25% - 10px);
  position: relative;
  border: 1px solid #ccc;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e9e9e9;
  cursor: pointer;
}
.gallery-item img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 5px;
}
.gallery-item .delete-btn {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #ff6b6b;
  color: white;
  border: none;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  font-size: 12px;
  cursor: pointer;
}

.error-message {
  color: red;
  font-size: 0.9em;
  display: none;
}

.modal {
  display: none; /* Скрываем по умолчанию */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); /* Полупрозрачный фон */
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.modal img {
  max-width: 80%;
  max-height: 80%;
  border-radius: 10px;
}
.close_btn {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: red;
  color: white;
  border: none;
  font-size: 20px;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.dnd-form {
  position: relative;
  width: 100%;
  height: 10rem;
  margin-bottom: 30px;
}

.overlapped {
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  width: 100%;
  opacity: 0.1;
}

.overlap {
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f3f3f3;
  border-radius: 4px;
  border: 1px dashed #007bff;
}
